.container {
    color: #ffffff;
}


.castBlock {
    padding: 25px 65px;
    background-color: #ad8b26a0;
}

.fromWhatMovie {
    display: flex;
    align-items: center;
    gap: 10px;

    img {
        width: 100px;
        height: auto;
    }

    p {
        font-weight: 700;
        font-size: 48px;
    }

    a {
        font-size: 24px;
        text-decoration: none;
        color: #484848;
    }
}

.crewAndCast {
    display: flex;
    padding: 25px 65px;
    background-color: #1a1a1a;

}

.title {
    font-weight: 700;
    font-size: 32px;
}

.cast {
    display: flex;
    flex-direction: column;
    gap: 25px;
    width: 50%;
}


.actorBlock {
    display: flex;
    align-items: center;
    gap: 25px;

    img {
        width: 100px;
        height: 100px;
        object-fit: cover;
        border-radius: 15px;
    }

    a {
        text-decoration: none;
        font-weight: 700;
        color: #ffffff;
        font-size: 24px;
    }

    p:nth-of-type(2) {
        font-weight: 500;
        font-size: 18px;
    }
}

.crew {
    display: flex;
    flex-direction: column;
    gap: 25px;

}


.personBlock {
    display: flex;
    align-items: center;
    gap: 25px;

    img {
        width: 100px;
        height: 100px;
        object-fit: cover;
        border-radius: 15px;
    }

    a {
        text-decoration: none;
        color: #ffffff;
        font-weight: 700;
        font-size: 24px;
    }

    p:nth-of-type(2) {
        font-weight: 500;
        font-size: 18px;
    }
}