.moviesBlock {
    background-color: #1a1a1a;
    padding: 15px 35px;

    .title {
        font-weight: 700;
        font-size: 32px;
        margin-bottom: 10px;
    }
}


.movies {
    display: flex;
    flex-wrap: wrap;
    gap: 25px;
}

.movie {
    flex: 1 2 250px;
    min-width: 250px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    border-radius: 8px;
    transition: all 0.3s ease;
    overflow: hidden;
    cursor: pointer;

    img {
        height: 100%;
        width: 100%;
        object-fit: cover;
        border-radius: 4px;

    }
    
    a {
        text-decoration: none;
        color: #fff;
        font-weight: 700;
        font-size: clamp(1rem, 1.25vw, 2.25rem);
    }
}


@media (max-width: 1200px) {
    .actor {
        flex: 1 1 250px;
        /* Блоки уменьшаются до 180px */
    }
}

@media (max-width: 992px) {
    .actor {
        flex: 1 1 200px;
        /* Блоки уменьшаются до 150px */
    }
}

@media (max-width: 768px) {
    .actor {
        flex: 1 1 120px;
        height: 100%;
    }
}